function populateList() {
  let list = document.querySelector(".todoList");
  for (let i = 0; i < todos.length; i++) {
    var li = document.createElement("li");
    li.appendChild(document.createTextNode(todos[i].todoText));
    /*li.addEventListener(
      "click",
      function () {
        todos[i].todoComplete = !todos[i].todoComplete;
      },
      false
    );
*/
    if (todos[i].todoComplete) {
      li.className = "done";
    }
    list.appendChild(li);
  }
}
